ul.list{
  list-style: none;
  
}
.background-menu{
    padding: 3.5rem 3rem;
    position: relative;
}

.header-menu{
    margin:-1rem 0 2rem 0;
    color:white;
    /* text-align: center; */
    font-size: 2.5rem;
}
.list-header-menu{
  color:white;
  font-size: 2rem;
  font-weight: 500;
  margin:16px 0;
}
.list-content-menu{
  color:rgba(255, 255, 255, 0.8);
  font-size: 1.5rem;
  font-weight: 400;
  margin:12px 0;
}
.price{
  color:rgba(255, 255, 255, 0.8);
  font-size: 1.6rem;
}
.img-menu{
  width: 16rem;
  height: 16rem;
  overflow: auto;
}

@media (max-width:35rem){
  .flex-menu{
    display: flex;
    flex-direction: column;
  }
  
}
@media (min-width:35rem){
  .flex-menu{
    display: flex;
  }
  .list-menu{
    margin-left: 10%;
  }
}

@media (max-width:48rem) {
    .header-menu{
      font-size: 2rem;
    }
    .content-about-us{
        font-size: 1.25rem;
    }
    .list-header-menu{
      font-size: 1.75rem;
    }
    .list-content-menu{
      font-size: 1.25rem;
    }
    .price{
      font-size: 1.35rem;
    }
    .img-menu{
      height: 12rem;
      width:12rem;
    }
  }


@media (max-width:28rem) {
    .background-menu{
      padding: 3rem 2.5rem;
      position: relative;
    }
    .header-menu{
      font-size: 1.75rem;
    }
    .content-about-us{
        font-size: 1rem;
    }
    .list-header-menu{
      font-size: 1.5rem;
    }
    .list-content-menu{
      font-size: 1rem;
    }
    .price{
      font-size: 1.1rem;
    }
    .img-menu{
      height: 10rem;
      width: 10rem;
    }
  }