.background-about-us{
    padding: 3.5rem 3rem;
    position: relative;
}

.header-about-us{
    margin:0px;
    /* text-align: center; */
    font-size: 2.5rem;
}
.content-about-us{
  color:rgba(0, 0, 0, 0.86);
    /* text-align: center; */
    font-weight: 500;
    font-size: 1.25rem;
}
@media (max-width:48rem) {
    .header-about-us{
      font-size: 2rem;
    }
    .content-about-us{
        font-size: 1.25rem;
    }
  }
@media (max-width:28rem) {
    .background-menu{
      padding: 3rem 2.5rem;
      position: relative;
    }
    .header-about-us{
      font-size: 1.75rem;
    }
    .content-about-us{
        font-size: 1rem;
    }
  }